<script>
import { mapState } from 'vuex';
import accountDetail from './accountDetail.vue';
import dynamicWall from './dynamicWall.vue';

export default {
  components: {
    dynamicWall,
    accountDetail,
  },
  computed: {
    ...mapState({
      switchoption: (state) => state.switchoption,
    }),
  },
  methods: {
    switchOption(payLoad) {
      this.$store.commit('switchOption', payLoad);
    },
  },
};
</script>

