<script>
import outhot from './outhot.vue';
import inhot from './inhot.vue';
import tracked from './tracked.vue';
import exposed from './exposed.vue';

export default {
  components: {
    outhot,
    inhot,
    tracked,
    exposed,
  },
  data() {
    return {
      option: '',
      orderBox: false,
      // 下方是多選用的
      value: [],
      options: [
        { name: '#標籤A', code: 'a' },
        { name: '#標籤B', code: 'b' },
        { name: '#標籤C', code: 'c' },
        { name: '#標籤D', code: 'd' },
        { name: '#標籤E', code: 'e' },
        { name: '#標籤F', code: 'f' },
      ],
    };
  },
  methods: {
    addTag(newTag) {
      const tag = {
        name: newTag,
        code: newTag.substring(0, 2) + Math.floor((Math.random() * 10000000)),
      };
      this.options.push(tag);
      this.value.push(tag);
    },
  },
};
